.splash {
	text-align: center;
	margin: 10% 0 0 0;
	box-sizing: border-box;
	.message {
		font-size: 72px;
		line-height: 72px;
		text-shadow: rgba(0, 0, 0, 0.5) 0 0 15px;
		text-transform: uppercase;
		font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
	}
	.fa-spinner {
		text-align: center;
		display: inline-block;
		font-size: 72px;
		margin-top: 50px;
	}
}
